import React from 'react';

import googlePlayBadge from '../resources/google_play_badge.png';

export default (props) => {
  return (
    <a href={props.url} target='_blank' rel='nofollow noopener noreferrer' >
    <div className='store-button'>
      <img src={googlePlayBadge} alt="Thoracle Google Play Button" width='152' height='59'></img>
    </div>
    </a>
  );
}
