import React, { Component, Fragment } from 'react';
import Image from 'react-bootstrap/Image';
import LinkIcon from '@material-ui/icons/Link';
import CheckIcon from '@material-ui/icons/Check';
import Button from 'react-bootstrap/Button';

const exampleImages = [
  {
    url: 'https://i.imgur.com/zftwqQG.png',
    description: '👉 Search this image on Thoracle to find out if Elon actually wrote such tweet!'
  }, {
    url: 'https://i.imgur.com/Qg9mNRa.png',
    description: '😯 Seems unbeliavable? Search on Thoracle to fact-check quickly!'
  }, {
    url: 'https://i.imgur.com/6WgHr0p.png',
    description: '🙄 Do you think if this might be fake news? Search on Thoracle to find out!'
  }, {
    url: 'https://i.imgur.com/jd3pthI.png',
    description: '🤔 Controversial news topic... Want to get the fact-checked info? Search on Thoracle to find an article with detailed analysis!'
  }, {
    url: 'https://i.imgur.com/sWBrWzk.png',
    description: '😉 Remember you can use Thoracle on ANY tweet screenshot!'
  }
]

class CopyUrlButton extends Component {
  state = { copied: false };
  timeoutIds = [];

  copyLink() {
    let link = this.props.url;
    const linkInputElement = document.createElement('textarea');
    linkInputElement.value = link;
    document.body.appendChild(linkInputElement);
    linkInputElement.select();
    document.execCommand("copy");
    linkInputElement.blur();
    document.body.removeChild(linkInputElement);
    this.setState({ copied: true });
    this.timeoutIds.forEach(timeoutId => clearTimeout(timeoutId));
    this.timeoutIds.length = 0;
    this.timeoutIds.push(setTimeout(() => {
      this.setState({ copied: false });
    }, 2000));
  }

  render() {
    const variant = this.state.copied ? 'success' : 'primary';
    const content = this.state.copied ? (
      <Fragment> <CheckIcon /> Copied! </Fragment>
     ) : (
      <Fragment> <LinkIcon /> Copy Image URL </Fragment>
    );
    return (
      <Button variant={variant} onClick={() => this.copyLink()} className='copy-url-button' >
        {content}
      </Button>
    );
  }
}

export default () => {
  const renderExampleImageDiv = (exampleImage) => (
    <div className='example-ss-wrapper' key={exampleImage.url}>
      <Image src={exampleImage.url} rounded className='example-ss' />
      <div className='example-description'>{exampleImage.description}</div>
      <div className='copy-url-button-wrapper'>
        <CopyUrlButton url={exampleImage.url}/>
      </div>
    </div>
  );
  return (
    <div className='content content-narrow'>
      <h1>How to use Thoracle?</h1>
      <p>Below are some screenshot examples for you to try out Thoracle and its features.</p>
      <p><i>
        &bull; Simply copy an image URL and search it in the 'Paste Image URL' tab on the home page.<br/>
        &bull; If you already downloaded the Chrome extension, you can perform the search by right-clicking on an image and selecting 'Search on Thoracle' option.<br/>
        <br/>
      </i></p>
      {exampleImages.map(exampleImage => renderExampleImageDiv(exampleImage))}
    </div>
  );
}
