import React from 'react';
import YouTube from 'react-youtube-embed'
import {TwitterFollowButton} from "react-twitter-embed";

export default () => {
  return (
    <div className='content content-narrow'>
      <h1>About</h1>
      <YouTube id='w9nv0fxeAcg' />
      <br />
      <p>Thoracle is a tool to find tweets from their screenshots. The tweets can be searched through web application, mobile application or chrome extension. After you find the tweet, you can browse it on Twitter. Thoracle also provides different kinds of information about the tweet: The author of the tweet is classified as a bot or genuine account, the relevant news articles about the tweet content are listed, and there is a comment section for discussing the validity of the tweet’s content.</p>
      <p>The main goal of the Thoracle project is preventing public manipulation by fake news through social media. We hope to achieve this goal by providing the fastest way for users from tweet screenshots to the actual tweets along with the analyses. It has been developed as a senior-year project by a group of 5 computer science students at Bilkent University.</p>
      <br />
      <div className='footer-about'>
        <p><a href='https://twitter.com/thoraclenet' target='_blank' rel='noopener noreferrer'>@ThoracleNet</a> <TwitterFollowButton screenName={'thoraclenet'}/> </p>
        <p><i>Thoracle ~ 2020</i></p>
      </div>
    </div>
  );
}
